.App {
  text-align: center;
  background-color: black;
  border: 3px solid lightyellow;
  border-radius: 12px;
  padding-bottom: 6px;
}
.app--button{
  background-color: darkgoldenrod; /* Green */
  border: none;
  border-radius: 4px;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 24px;
  margin : 5px;
}
.app-image{
  border-radius: 12px;
  
  max-width: 100%;
  width: 400px;
  height: auto;
}
.app--showName{
  margin-left: 62px;
  font-size: 3rem;
  color: white;
  text-align: left;
font-family: sans-serif;
}
.app--showLang{
  margin-left: 62px;
  font-size: 2rem;
  color: white;
text-align: left;
font-family: sans-serif;
}
.app--showType{
  margin-left: 62px;
  font-size: 2rem;
  color: white;
  font-family: sans-serif;
  text-align: left;
}

.Card{
  background-color: darkslategray;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
 color: white;
 width: auto;
 max-width: fit-content;
 padding-bottom: 12px;
}
.card--img{
  display: grid;
  place-items: center;
}
.Card--image{

  width: 534px;
  max-width: 100%;
  border-radius: 20px;
}
.Card--section{
  font-size: xx-large;
 
    margin-left: 25px;
}

.Card--name ,.Card--ratings,.Card--genres,.Card--language,.Card--schedule{
  margin: 0px;
  padding: 0px;
}
.Card--schedule{
  font-size: 2rem;
}
.Card--summary{
  font-size: 1rem;
  margin: 0px;
  margin-left: 15px;
  margin-top: 30px;
}
#user{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin :16px;
  background-color: rgb(3, 54, 54);
  border-radius: 12px;
}

.Card--form--title{
  margin-top: 4px;
  font-size: xx-large;
  text-align: center;
}
.Card--form--inputSection{
  display: flex;
  flex-direction: column;
  margin: 20px;
}
.Card--button{
  display: block;
  justify-self: center;
  background-color: darkgoldenrod; /* Green */
  border: none;
  border-radius: 12px;
  color: white;
  padding: 15px 32px;
  text-decoration: none;
  font-size: 24px;
  margin: auto;
}